// =============================================================================
// > ADMIN : ACF-LIGHT-REPEATER
// =============================================================================

.acf-light-repeater {
    & > .acf-input > .acf-repeater {
        & > .acf-table {
            border: 0;
            &> tbody > .acf-row {
                background: white;
                & > .acf-row-handle {
                    @include size(10px);
                    padding: 0;
                    opacity: 0;
                    // &:hover
                    //     opacity: 1
                    &.order {
                        height: 15px;
                        line-height: 15px;
                        @include font(10px); } }
                &:hover > .acf-row-handle {
                    opacity: 1; }
                & > .acf-fields {
                    border: 0;
                    & > .acf-field {
                        &.acf-field-flexible-content,
                        &.acf-field-repeater {
                            padding: 5px; } }
                    & > .acf-field > .acf-input > div > {
                        .acf-actions {
                            @include flex(row nowrap, center);

                            .button {
                                display: block;
                                text-shadow: none;
                                background: #F5F5F5;
                                border: 1px solid #F5F5F5;
                                box-shadow: none;
                                @include font(white);
                                @include size(30px);
                                position: relative;
                                border-radius: 50%;
                                &::before {
                                    content: "+";
                                    @include absolute(top 0 left 0);
                                    @include size(100%);
                                    @include font(20px 25px #72777c center); }
                                &:hover {
                                    background: color(primary);
                                    @include font(primary);
                                    border-color: color(primary);
                                    &::before {
                                        @include font(white); } } } } } } } } } }



