// =============================================================================
// > ADMIN
// =============================================================================

.acf-choice .acf-input {

    .acf-radio-list {
        @include flex(row wrap); }

    li {
        position: relative;
        @include size(40px);
        min-width: 40px;
        margin: 3px !important; }

    label {
        text-indent: -9999px;
        display: block; }

    input {
        @include absolute(top 0 left 0);
        @include size(40px);
        opacity: .8;
        transition: all .25s ease;
        outline: 0 !important;
        box-shadow: none !important;
        border-color: #b4b9be !important;
        transform: scale(.5);
        &::before {
            display: none; }
        &:checked {
            opacity: 1 !important;
            transform: scale(1);
            border-color: color(primary) !important;
            &::after {
                color: color(primary) !important; } }

        // ========== COMMON ICONS ========== //
        &[value="none"],
        &[value="never"],
        &[value="image"] {
            border-color: white !important;
            &::after {
                @include font(40px 40px center #666);
                @include absolute(top 0 left 0);
                @include size(100%); } }

        &[value="none"]::after,
        &[value="never"]::after {
            @include fa(times); }
        &[value="image"]::after {
            @include fa(image); } } }

// ==================================================
// > COLORS
// ==================================================
.acf-choice--color .acf-input input {
    @each $color, $value in $colors {
        &[value="#{$color}"] {
            background: $value !important; } }

    &[value="custom"] {
        background: conic-gradient(red, yellow, lime, aqua, blue, magenta, red) !important; } }

// ==================================================
// > EDGES
// ==================================================
.acf-choice--edge .acf-input input {
    &[value="in-ascending"],
    &[value="in-descending"],
    &[value="out-ascending"],
    &[value="out-descending"] {
        @include size(50px, 30px);
        border-radius: 0;
        border: 0 !important;
        background: #ccc;
        position: absolute !important;
        opacity: .4; } }

.acf-choice--edge--top .acf-input input {
    &::before {
        display: block;
        border-radius: 0 !important;
        background: none !important;
        margin: 0 !important; }
    &[value="in-ascending"] {
        @extend .has-edge-top--in-ascending; }
    &[value="in-descending"] {
        @extend .has-edge-top--in-descending; }
    &[value="out-ascending"] {
        @extend .has-edge-top--out-ascending; }
    &[value="out-descending"] {
        @extend .has-edge-top--out-descending; }
    &[value="in-ascending"]::before,
    &[value="out-descending"]::before {
        border-right-width: 50px; }
    &[value="in-descending"]::before,
    &[value="out-ascending"]::before {
        border-left-width: 50px; }
    &[value="in-ascending"]::before,
    &[value="in-descending"]::before {
        border-top-width: 20px; }
    &[value="out-ascending"]::before,
    &[value="out-descending"]::before {
        border-bottom-width: 20px; } }

.acf-choice--edge--bottom .acf-input input {
    &[value="in-ascending"] {
        @extend .has-edge-bottom--in-ascending; }
    &[value="in-descending"] {
        @extend .has-edge-bottom--in-descending; }
    &[value="out-ascending"] {
        @extend .has-edge-bottom--out-ascending; }
    &[value="out-descending"] {
        @extend .has-edge-bottom--out-descending; }
    &[value="in-ascending"]::after,
    &[value="out-descending"]::after {
        border-left-width: 50px; }
    &[value="in-descending"]::after,
    &[value="out-ascending"]::after {
        border-right-width: 50px; }
    &[value="in-ascending"]::after,
    &[value="in-descending"]::after {
        border-bottom-width: 20px; }
    &[value="out-ascending"]::after,
    &[value="out-descending"]::after {
        border-top-width: 20px; } }

// ==================================================
// > ICONS
// ==================================================
.acf-choice--icons .acf-input input {
    border: 0 !important;
    &::after {
        @include font(40px 40px center #666);
        @include absolute(top 0 left 0);
        @include size(100%); }

    &[value="start"]::after {
        @include far(arrow-to-top); }
    &[value="center"]::after {
        @include fad(border-center-h); }
    &[value="end"]::after {
        @include far(arrow-to-bottom); }

    &[value="column-md"]::after,
    &[value="column-reverse-md"]::after {
        @include fal(desktop); }
    &[value="column-sm"]::after,
    &[value="column-reverse-sm"]::after {
        @include fal(tablet); }
    &[value="column-xs"]::after,
    &[value="column-reverse-xs"]::after {
        @include fal(mobile); }

    &[value="column-md"],
    &[value="column-reverse-md"],
    &[value="column-sm"],
    &[value="column-reverse-sm"],
    &[value="column-xs"],
    &[value="column-reverse-xs"] {
        &::before {
            @include fa(arrow-down);
            display: block !important;
            @include size(2em);
            background: #999;
            border-radius: 50%;
            @include font(1 2 center white);
            @include absolute(bottom -2em right -1em);
            z-index: 10;
            transition: all .35s ease; }
        &:checked::before {
            background: color(primary);
            bottom: -1em; } }
    &[value="column-reverse-md"]::before,
    &[value="column-reverse-sm"]::before,
    &[value="column-reverse-xs"]::before {
        content: fa-content(arrow-up); }

    &[value="fadein"]::after {
        @include far(eye-slash); }
    &[value="fadein-left"]::after {
        @include far(long-arrow-right); }
    &[value="fadein-right"]::after {
        @include far(long-arrow-left); }
    &[value="fadein-top"]::after {
        @include far(long-arrow-down); }
    &[value="fadein-bottom"]::after {
        @include far(long-arrow-up); }
    &[value="fadein-sides"]::after {
        @include far(compress-arrows-alt); }

    &[value="cover"]::after {
        @include far(expand-arrows-alt); } }



