// =============================================================================
// > ADMIN : ACF-HORIZONTAL-REPEATER
// =============================================================================

.acf-horizontal-repeater {
    & > .acf-input > .acf-repeater {
        padding-right: 40px;

        & > .acf-table > tbody {
            @include flex(row nowrap);
            & > .acf-row:not(.acf-clone) {
                flex: 1;
                &,
                & > .acf-fields {
                    @include flex(column nowrap);
                    // flex-grow: 1

                    & > .acf-row-handle {
                        order: -1; } } }

            & > .acf-row {
                &,
                & > td {
                    width: 100% !important;
                    box-sizing: border-box;
                    & > .acf-field {
                        width: 100%  !important;
                        height: auto !important; } }

                & > td > .acf-icon {
                    &.-plus {
                        left: -9px;
                        top: 6px; }
                    &.-minus {
                        left: 50%; } } }

            & > tr {
                & > td {
                    border-width: 0 0 0 1px; }
                &:first-child > td {
                    border-width: 0; } } }

        & > .acf-actions {
            @include absolute(top 50% right 5px);
            margin-top: -30px;
            .button {
                text-shadow: none;
                background: #F5F5F5;
                border: 1px solid #F5F5F5;
                box-shadow: none;
                @include font(#F5F5F5);
                @include size(30px);
                position: relative;
                border-radius: 50%;
                &::before {
                    content: "+";
                    @include absolute(top 0 left 0);
                    @include size(100%);
                    @include font(20px 25px #72777c center); }
                &:hover {
                    background: color(primary);
                    @include font(primary);
                    border-color: color(primary);
                    &::before {
                        @include font(white); } } } } } }



