// =============================================================================
// > ADMIN : ACF-SECTIONS-CONTENT
// =============================================================================

.acf-sections-content > .acf-input > .acf-flexible-content > .values > .layout {

    // ==================================================
    // > HANDLE
    // ==================================================
    & > .acf-fc-layout-handle {
        background: #F5F5F5;
        &::before {
            @include fa(th-large);
            @include font(16px primary);
            margin-right: .5em; } }

    @each $layout, $icon in $section-content-icons {
        &[data-layout="#{$layout}"] > .acf-fc-layout-handle::before {
            content: fa-content($icon); } } }


// ==================================================
// > DROPDOWN
// ==================================================

.acf-tooltip a[data-layout]::before {
    @include fa(th-large);
    margin-right: .5em;
    width: 1.5em;
    display: inline-block;
    @include font(center);
    border-right: 1px solid rgba(white, .5em);
    margin-right: .5em;
    padding-right: .25em; }


@each $layout, $icon in $section-content-icons {
    .acf-tooltip a[data-layout="#{$layout}"]::before {
        content: fa-content($icon); } }



